import React, { Fragment } from "react";
import {
  MdAccountBox,
  MdAccountCircle,
  MdAdd,
  MdCheckCircle,
  MdInfoOutline,
  MdOutlinePassword,
  MdWarningAmber,
} from "react-icons/md";
import { connect } from "react-redux";
import {
  Auth,
  Basic,
  FC_GetUserProfile,
  UserDetailsByNID,
} from "../../actions";
import {
  FC_CreatePermutation,
  FC_GetMyPermutation,
  FC_SearchPermutationByCode,
  FC_UserApprovePermutation,
  PermutationItem__Interface,
  SendMutationData,
  SendPermutation__Interface_Res,
} from "../../actions/permutation.action";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import DateEligibleErrorDisplay from "../../components/DateEligibleErrorDisplay/DateEligibleErrorDisplay";
import NonValidatedErrorDisplay from "../../components/DateEligibleErrorDisplay/NonValidatedErrorDisplay";
import Heading from "../../components/Heading/Heading";
import TextInput from "../../components/Inputs/TextInput";
import Loading from "../../components/Loading/Loading";
import { StoreState } from "../../reducers";
import { validateAuthUser } from "../../utils/validatedStaff";
import HeaderSwapping from "./HeaderSwapping";
import PermutationItem from "./PermutationItem";
import TdSwapingItem from "./TdSwapingItem";

// props for the component
interface AppProps {
  auth: Auth;
  basic: Basic;
}

interface AppState {
  loading: boolean;
  selected_option: 1 | 2 | null;
  search_nid: string;
  loading_user: boolean;
  user_2: UserDetailsByNID | null;
  error: {
    target:
      | "main-error"
      | "main-success"
      | "search-user-error"
      | "permutation-error"
      | "search-code-error"
      | "approve-permutation-error"
      | "approve-permutation-success"
      | null;
    msg: string;
  };
  loading_request: boolean;
  transfer_success: boolean;
  res_data: SendPermutation__Interface_Res | null;
  // step 2
  search_code: string;
  loading_user_by_code: boolean;
  permutation: any | null;
  found_permutations: PermutationItem__Interface[];
  my_permutations: PermutationItem__Interface[];
  loading_permutations: boolean;
  selected_permutation: null | string;
  loading_approve_permutation: boolean;
  loading_reject_permutation: boolean;
  selected_permutation_list: string;
  has_request_transfer: boolean;
}

class _CandidateSwaping extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      selected_option: null,
      search_nid: "",
      loading_user: false,
      user_2: null,
      error: {
        msg: "",
        target: null,
      },
      loading_request: false,
      transfer_success: false,
      res_data: null,
      // data
      search_code: "",
      loading_user_by_code: false,
      permutation: null,
      found_permutations: [],
      my_permutations: [],
      loading_permutations: false,
      selected_permutation: null,
      loading_approve_permutation: false,
      selected_permutation_list: "",
      has_request_transfer: false,
      loading_reject_permutation: false,
    };
  }

  setTab = (itm: 1 | 2 | null) => this.setState({ selected_option: itm });

  cleanError = () => {
    setTimeout(() => {
      this.setState({
        error: {
          msg: "",
          target: null,
        },
      });
    }, 2000);
  };

  componentDidMount = () => {
    this.appStart();
  };
  appStart = () => {
    console.log("loading the data");

    if (!this.props.auth.user) {
      this.cleanError();
      return this.setState({
        error: {
          target: "main-error",
          msg: "Failed to load your permutations, please try again later",
        },
      });
    }

    if (this.props.basic.academic_calender === null) {
      this.setState({
        error: {
          target: "main-error",
          msg: "Academic year does not found!",
        },
      });

      setTimeout(() => {
        this.appStart();
      }, 1000);
      return;
    }

    this.setState({
      loading_permutations: true,
    });
    FC_GetMyPermutation(
      this.props.auth.user.user_id,
      this.props.basic.academic_calender.academic_year_id,
      (status, data, msg) => {
        if (status === true) {
          // console.log(data);
          if (!data) {
            this.cleanError();
            return this.setState({
              loading_permutations: false,
              error: {
                target: "main-error",
                msg: msg,
              },
            });
          }
          this.setState({
            loading_permutations: false,
            my_permutations: data.user_permutations,
            has_request_transfer: data.has_request_transfer ? true : false,
            error: {
              target: null,
              msg: "",
            },
          });
        } else {
          this.cleanError();
          this.setState({
            loading_permutations: false,
            error: {
              target: "main-error",
              msg: msg,
            },
          });
        }
      }
    );
  };

  searchUser = (e: any) => {
    e.preventDefault();

    if (!this.props.auth.user) {
      this.setState({
        error: {
          target: "search-user-error",
          msg: "You are not authenticated, please refresh your browser",
        },
      });
      this.cleanError();
      return;
    }

    // Check the nid length
    if (this.state.search_nid.length !== 16) {
      this.setState({
        error: {
          target: "search-user-error",
          msg: "National Id must be 16 digits",
        },
      });
      this.cleanError();
      return;
    }

    console.log("submit search");

    if (this.props.auth.user.nid === this.state.search_nid) {
      this.setState({
        error: {
          target: "search-user-error",
          msg: "You can't search using your own NID, please use the NID of the other teacher",
        },
      });
      this.cleanError();
      return;
    }

    //* Success validation
    this.setState({
      loading_user: true,
      error: {
        target: null,
        msg: "",
      },
    });

    FC_GetUserProfile(this.state.search_nid, (status, data, msg) => {
      if (status === true) {
        if (data === null) {
          this.setState({
            user_2: data,
            loading_user: false,
            error: {
              target: "search-user-error",
              msg: data === null ? "The NID you are using is not valid" : msg,
            },
          });
        } else {
          // check if they have the same position
          if (data.position_code !== this.props.auth.position?.position_code) {
            this.setState({
              user_2: null,
              loading_user: false,
              error: {
                target: "search-user-error",
                msg: `You don't have the same position with ${data.full_name}, the permutation can't be done`,
              },
            });
          } else {
            this.setState({
              user_2: data,
              loading_user: false,
            });
          }
        }
      } else {
        this.setState({
          loading_user: false,
          error: {
            target: "search-user-error",
            msg: data === null ? "The NID you are using is not valid" : msg,
          },
        });
      }
    });
  };

  userApprovePermutationUser = (perm_id: string | null) => {
    if (perm_id === null) {
      return;
    }

    if (!window.confirm("Are you sure you want to confirm this permutation?")) {
      return;
    }

    this.setState({
      loading_approve_permutation: true,
    });
    FC_UserApprovePermutation(
      {
        permutation_id: perm_id,
        permutation_status: "Accepted",
      },
      (status, data, msg) => {
        if (status === true) {
          let foundPermutation = this.state.found_permutations.find(
            (itm) => itm.permutation_id === perm_id
          );

          if (foundPermutation !== undefined) {
            foundPermutation = {
              ...foundPermutation,
              permutation_status: "Accepted",
            };
          }
          // this.state.selected_option

          this.setState({
            loading_approve_permutation: false,
            found_permutations: [],
            my_permutations:
              foundPermutation === undefined
                ? [...this.state.my_permutations]
                : [...this.state.my_permutations, foundPermutation],
            error: {
              target: "approve-permutation-success",
              msg: "Your permutation has been approved successfully",
            },
            selected_option: null,
            // found_permutations
          });

          // setTimeout(() => {
          //   this.appStart();
          // }, 2000);
        } else {
          this.setState({
            error: {
              target: "approve-permutation-error",
              msg: msg,
            },
            loading_approve_permutation: false,
          });
        }
      }
    );
  };

  rejectPermutationUser = (perm_id: string | null) => {
    if (perm_id === null) {
      return;
    }

    if (!window.confirm("Are you sure you want to reject this permutation?")) {
      return;
    }

    this.setState({
      loading_reject_permutation: true,
    });
    FC_UserApprovePermutation(
      {
        permutation_id: perm_id,
        permutation_status: "Rejected",
      },
      (status, data, msg) => {
        if (status === true) {
          this.setState({
            loading_reject_permutation: false,
            my_permutations: this.state.my_permutations.map((itm) =>
              itm.permutation_id === perm_id
                ? { ...itm, permutation_status: "Rejected" }
                : itm
            ),
            error: {
              target: "approve-permutation-success",
              msg: "Your permutation has been rejected successfully",
            },
            // selected_option: null,
          });
        } else {
          this.setState({
            error: {
              target: "approve-permutation-error",
              msg: msg,
            },
            loading_reject_permutation: false,
          });
        }
      }
    );
  };

  generatePermutation = () => {
    if (
      !window.confirm(
        `Are you sure ${this.state.user_2?.full_name} is aware of this request?`
      )
    )
      return window.alert(
        `Please contact ${this.state.user_2?.full_name} and make sure he/she agree with your permutation request`
      );

    if (!this.props.auth.user || !this.props.auth.school)
      return this.setState({
        error: {
          target: "permutation-error",
          msg: "Please reload your browser",
        },
      });

    if (!this.props.basic.academic_calender)
      return this.setState({
        error: {
          target: "permutation-error",
          msg: "Academic information can't be loaded, please reload your browser",
        },
      });

    if (this.state.user_2 === null)
      return this.setState({
        error: {
          target: "permutation-error",
          msg: "Academic information can't be loaded, please reload your browser",
        },
      });

    let data: SendMutationData = {
      // academic_year_id: this.props.auth.country,
      academic_year_id: this.props.basic.academic_calender?.academic_year_id,
      position_code: this.state.user_2.position_code
        ? this.state.user_2.position_code
        : "",
      qualification_id: this.state.user_2.qualification_id
        ? this.state.user_2.qualification_id
        : "",
      role_id: this.state.user_2.role_id ? this.state.user_2.role_id : "",
      school_one: this.props.auth.school.school_code,
      school_two: this.state.user_2.school_code
        ? this.state.user_2.school_code
        : "",
      user_one: this.props.auth.user.user_id,
      user_two: this.state.user_2.user_id ? this.state.user_2.user_id : "",
    };

    this.setState({ loading_request: true });
    FC_CreatePermutation(data, (status, data, msg) => {
      if (status === true) {
        this.setState({
          loading_request: false,
          res_data: data,
          transfer_success: true,
        });
      } else {
        this.setState({
          loading_request: false,
          error: {
            msg: msg,
            target: "permutation-error",
          },
        });
      }
    });
  };

  // Add stuffs
  searchUserByCode = () => {
    //* Success validation
    this.setState({
      loading_user_by_code: true,
      error: {
        target: null,
        msg: "",
      },
      found_permutations: [],
    });

    FC_SearchPermutationByCode(this.state.search_code, (status, data, msg) => {
      if (status === true) {
        if (data.length <= 0) {
          console.log("no data found");

          this.setState({
            found_permutations: [],
            loading_user_by_code: false,
            error: {
              target: "search-code-error",
              msg: "No permutation request found",
            },
          });
          // this.cleanError();
        } else {
          this.setState({
            found_permutations: data,
            loading_user_by_code: false,
          });
        }
      } else {
        this.setState({
          found_permutations: [],
          loading_user_by_code: false,
          error: {
            target: "search-code-error",
            msg: msg,
          },
        });
        this.cleanError();
      }
    });
  };

  render() {
    //// if (this.state.loading_permutations === true) {
    ////   return <Loading />
    //// }

    if (!this.props.auth.has_one_year) {
      return (
        <DateEligibleErrorDisplay
          title="You are not qualified to perform a permutation"
          name="permutation"
        />
      );
    }

    let ProfileValidation = validateAuthUser(this.props.auth);
    if (
      ProfileValidation.errors.length >= 1 &&
      ProfileValidation.validated === false
    )
      return (
        <NonValidatedErrorDisplay
          title="transfer"
          errors={ProfileValidation.errors}
        />
      );

    if (this.state.has_request_transfer === true)
      return (
        <DashboardContainer className="max-w-screen-md mx-auto py-4 text-center animate__animated animate__zoomIn animate__faster">
          <h2 className=" text-6xl text-center text-red-400 pb-4">
            <MdInfoOutline className="inline-block" />
          </h2>
          <h4 className="text-xl font-bold">
            It looks like you have already requested for a transfer, <br />
            You can't Request a permutation
          </h4>
          <p className="text-gray-600 pt-2">
            If you want to request for a permutation, <br /> you will need to
            remove your transfer.
          </p>
        </DashboardContainer>
      );

    let current = this.state.my_permutations.find(
      (itm) =>
        itm.academic_year_id ===
          this.props.basic.academic_calender?.academic_year_id &&
        itm.permutation_status !== "Rejected"
    );

    if (this.state.transfer_success === true)
      return (
        <DashboardContainer className="py-2 animate__animated animate__zoomIn animate__faster">
          <div className="text-center">
            <MdCheckCircle className="text-green-500 mx-auto" size={120} />

            <h2 className="text-2xl font-bold pt-2 text-green-600">
              Your Permutation request has been created!
            </h2>
            <p className="py-2 text-gray-600">
              Now you need to send the code below to{" "}
              <b>{this.state.user_2?.full_name}</b> <br />
              so that he/she can approve this transfer
            </p>
            <div className="mx-auto rounded my-1 border py-2 inline-block px-5 bg-gray-50">
              <h4 className="text-gray-500">Permutation code:</h4>
              <h1 className="text-3xl font-bold">
                {this.state.res_data?.permutation_code}
              </h1>
            </div>
          </div>
          <div className="text-right border-t py-3 mt-4">
            {/* <Button
              theme="accent-light"
              onClick={() =>
                this.setState({
                  transfer_success: false,
                })
              }
            >
              Go to permutation list
            </Button> */}
          </div>
        </DashboardContainer>
      );

    if (!this.props.auth.user)
      return (
        <DashboardContainer>
          <Alert>Please refresh your account or login</Alert>
        </DashboardContainer>
      );

    return (
      <>
        {this.state.error.target === "main-error" && (
          <Alert theme="danger">{this.state.error.msg}</Alert>
        )}

        {this.state.selected_option === null && (
          <>
            {current === undefined &&
              this.state.loading_permutations === false && (
                <DashboardContainer className="animate__animated animate__zoomIn animate__faster">
                  <Heading className=" py-2 lg:pb-3 lg:pt-2 font-light text-center text-gray-500">
                    Permutations
                  </Heading>

                  <div className="pt-2 flex gap-3 flex-col lg:flex-row">
                    {[
                      {
                        icon: MdAdd,
                        title: "Generate new permutation request",
                        id: 1,
                        description:
                          "You will need the id of the teacher you want to swap with.",
                        onClick: () => this.setTab(1),
                      },
                      {
                        icon: MdOutlinePassword,
                        title: "Find permutation by code",
                        id: 2,
                        description:
                          "You will need a code, that was generated by the other teacher",
                        onClick: () => this.setTab(2),
                      },
                    ].map((itm, i) => {
                      let Icon = itm.icon;
                      return (
                        <div
                          key={i}
                          onClick={itm.onClick}
                          className="p-2  group items-center rounded border flex flex-1 hover:bg-accent-400 cursor-pointer"
                        >
                          <section className="p-2 bg-accent-400 rounded group-hover:bg-accent-800 group-hover:text-white text-accent-800">
                            <Icon size={40} />
                          </section>
                          <section className="flex-1 pl-3">
                            <h2 className="text-xl  text-gray-900">
                              {itm.title}
                            </h2>
                            <p className="text-gray-400 text-sm">
                              {itm.description}
                            </p>
                          </section>
                        </div>
                      );
                    })}
                  </div>
                </DashboardContainer>
              )}

            {this.state.loading_permutations === true && (
              <div className="py-6 text-center text-6xl text-accent-800">
                <Loading className="mx-auto text-2xl" />
              </div>
            )}

            {!(this.state.my_permutations.length === 0) && (
              <DashboardContainer
                transparent
                className="mt-3 animate__animated animate__fadeIn animate__faster"
              >
                <h2 className="text-center text-3xl pb-2 font-bold text-gray-500">
                  My permutations{" "}
                  <b>{`(${this.state.my_permutations.length})`}</b>
                </h2>
                {this.state.loading_permutations === true ? (
                  <div className="py-6 text-center text-8xl">
                    <Loading className="mx-auto text-2xl" />
                  </div>
                ) : this.state.my_permutations.length <= 0 ? (
                  <div className="py-4 text-center bg-white">
                    <h2 className="text-6xl text-gray-600">
                      <MdWarningAmber className="mx-auto" />
                    </h2>

                    <h3 className="font-bold text-lg">No permutation found!</h3>
                    <h3 className="pt-1 text-gray-600">
                      Please try again later
                    </h3>
                  </div>
                ) : (
                  <>
                    <div className="p-1 rounded">
                      <table className=" w-full rounded">
                        <thead>
                          <tr className="bg-white">
                            <th className="border p-2 text-left">#</th>
                            <th className="border p-2 text-left bg-blue-50">
                              Candidate 1{" "}
                              <span className="text-xs bg-yellow-500 px-2 py-0.5 rounded-full">
                                Initiator
                              </span>
                            </th>
                            <th className="border p-2 text-left bg-blue-50">
                              School 1
                            </th>
                            <th className="border p-2 text-left bg-blue-50">
                              Candidate 1
                            </th>
                            <th className="border p-2 text-left bg-blue-50">
                              School 1
                            </th>
                            <th className="border p-2 text-left">
                              Users status
                            </th>
                            {/* <th className="border p-2 text-left">REB status</th>
                            <th className="border p-2 text-left">Code</th> */}
                            <th className="border p-2 text-left">Dates</th>
                          </tr>
                        </thead>

                        <tbody>
                          {/* New permutations */}
                          {this.state.my_permutations
                            .filter(
                              (itm) =>
                                itm.permutation_status !== "Rejected" &&
                                itm.permutation_status !== "Pending"
                            )
                            .map((el: PermutationItem__Interface, i) => {
                              if (
                                this.state.selected_permutation_list ===
                                el.permutation_id
                              )
                                return (
                                  <tr key={i} className="">
                                    <td colSpan={30} className="pt-1">
                                      <PermutationItem
                                        more
                                        permutation={el}
                                        // approveUser={this.userApprovePermutationUser}
                                        userRejectPermutation={
                                          el.user_two ===
                                            this.props.auth.user?.user_id &&
                                          el.permutation_status ===
                                            "Accepted" &&
                                          el.academic_year_id ===
                                            this.props.basic.academic_calender
                                              ?.academic_year_id &&
                                          el.reb_status === "Pending"
                                            ? this.rejectPermutationUser
                                            : undefined
                                        }
                                        loading_reject_permutation={
                                          this.state.loading_reject_permutation
                                        }
                                      />
                                    </td>
                                  </tr>
                                );
                              return (
                                <TdSwapingItem
                                  i={i}
                                  permutation={el}
                                  selectPermutation={(selected) =>
                                    this.setState({
                                      selected_permutation_list: selected,
                                    })
                                  }
                                />
                              );
                            })}

                          {/* Others permutation */}
                          {this.state.my_permutations
                            .filter(
                              (itm) => itm.permutation_status !== "Accepted"
                            )
                            .map((el: PermutationItem__Interface, i) => {
                              // return <PermutationItem key={i} permutation={el} />;

                              if (
                                this.state.selected_permutation_list ===
                                el.permutation_id
                              )
                                return (
                                  <tr key={i} className="">
                                    <td colSpan={30} className="pt-1">
                                      <PermutationItem
                                        more
                                        permutation={el}
                                        // approveUser={this.userApprovePermutationUser}
                                        userRejectPermutation={
                                          el.user_two ===
                                            this.props.auth.user?.user_id &&
                                          el.permutation_status ===
                                            "Accepted" &&
                                          el.academic_year_id ===
                                            this.props.basic.academic_calender
                                              ?.academic_year_id &&
                                          el.reb_status === "Pending"
                                            ? this.rejectPermutationUser
                                            : undefined
                                        }
                                        loading_reject_permutation={
                                          this.state.loading_reject_permutation
                                        }
                                      />
                                    </td>
                                  </tr>
                                );
                              return (
                                <TdSwapingItem
                                  i={i}
                                  permutation={el}
                                  selectPermutation={(selected) =>
                                    this.setState({
                                      selected_permutation_list: selected,
                                    })
                                  }
                                />
                              );
                            })}
                        </tbody>
                      </table>
                    </div>
                  </>
                )}
              </DashboardContainer>
            )}
          </>
        )}

        {this.state.selected_option === 1 && (
          <DashboardContainer className="animate__animated animate__zoomIn animate__faster">
            <HeaderSwapping
              title="Generate new permutation request"
              back={() => {
                if (this.state.user_2 !== null) {
                  return this.setState({
                    user_2: null,
                  });
                }
                this.setTab(null);
              }}
            />

            {this.state.user_2 === null && (
              <div className="py-6 text-center">
                <MdAccountCircle size={70} className="mx-auto text-gray-400" />
                <h3 className="font-bold text-gray-700 text-2xl pt-2">
                  Type the ID of the teacher you want to swap with?
                </h3>
                <p className="pt-1 text-gray-600">
                  Make sure that he/she is aware that you are choosing him/her
                  <br />
                  If he/she does not know the about this your request will not
                  reach him/her
                </p>

                <div className="max-w-screen-sm mx-auto mt-3 ">
                  {this.state.error.target === "search-user-error" && (
                    <Alert theme="danger">{this.state.error.msg}</Alert>
                  )}

                  <div>
                    <form
                      onSubmit={this.searchUser}
                      className={`pt-2 flex gap-1 items-center pb-1 w-full bg-gray-100 px-3 rounded  ${
                        this.state.error.target === "search-user-error"
                          ? "animate__animated  animate__shakeX"
                          : ""
                      }`}
                    >
                      <TextInput
                        onChange={(e) => {
                          if (this.state.search_nid.length < 17)
                            this.setState({ search_nid: e.target.value });
                        }}
                        className="flex-1"
                        value={this.state.search_nid}
                        error={""}
                        label={""}
                        placeholder={""}
                        name={""}
                        type="number"
                      />
                      <Button
                        size="lg"
                        className="w-48"
                        loading={this.state.loading_user}
                        type="submit"
                      >
                        Search
                      </Button>
                    </form>
                  </div>
                </div>
              </div>
            )}

            {this.state.user_2 !== null && (
              <>
                {" "}
                {this.state.loading_user === true ? (
                  <div className="py-6 mx-auto text-center">
                    <Loading />
                  </div>
                ) : (
                  <div className="p-2 animate__animated animate__fadeIn animate__faster">
                    <div className=" flex pt-2">
                      <div className="">
                        <MdAccountBox size={120} className="text-gray-400" />
                      </div>
                      <div className="pl-3 flex-1">
                        <h3 className="pb-0.5 text-xl font-bold text-gray-600 border-b mb-2">
                          User details
                        </h3>
                        <section className="flex items-center pb-0.5">
                          <label className="text-sm text-gray-500 mb-0">
                            NID:
                          </label>
                          <h4 className="font-bold pl-1">
                            {this.state.user_2.nid}
                          </h4>
                        </section>
                        <section className="flex items-center pb-0.5">
                          <label className="text-sm text-gray-500 mb-0">
                            Staff code:
                          </label>
                          <h4 className="font-bold pl-1">
                            {this.state.user_2.staff_code}
                          </h4>
                        </section>
                        <section className="flex items-center pb-0.5">
                          <label className="text-sm text-gray-500">
                            Teacher names:
                          </label>
                          <h4 className="font-bold pl-1">
                            {this.state.user_2.full_name}
                          </h4>
                        </section>
                      </div>
                      <div className="pl-3 flex-1">
                        <h3 className="pb-0.5 text-xl font-bold text-gray-600 border-b mb-2">
                          Academic details
                        </h3>
                        <section className="flex items-center pb-0.5">
                          <label className="text-sm text-gray-500 mb-0">
                            School code:
                          </label>
                          <h4 className="font-bold pl-1">
                            {this.state.user_2.school_code}
                          </h4>
                        </section>
                        <section className="flex items-center pb-0.5">
                          <label className="text-sm text-gray-500">
                            School name:
                          </label>
                          <h4 className="font-bold pl-1">
                            {this.state.user_2.school_name}
                          </h4>
                        </section>

                        <section className="flex items-center pb-0.5">
                          <label className="text-sm text-gray-500">
                            School location:
                          </label>
                          <h4 className="font-bold pl-1">
                            {this.state.user_2.district_name}
                          </h4>
                        </section>

                        <section className="flex items-center pb-0.5">
                          <label className="text-sm text-gray-500 mb-0">
                            Position code:
                          </label>
                          <h4 className="font-bold pl-1">
                            {this.state.user_2.position_code}
                          </h4>
                        </section>
                      </div>
                    </div>
                    <div className="p-1 text-right border-t pt-2 mt-2">
                      <Button
                        onClick={this.generatePermutation}
                        loading={this.state.loading_request}
                      >
                        Generate permutation request
                      </Button>
                    </div>
                  </div>
                )}
              </>
            )}
          </DashboardContainer>
        )}

        {this.state.selected_option === 2 && (
          <>
            {this.state.found_permutations.length === 0 ? (
              <DashboardContainer className="animate__animated animate__zoomIn animate__faster">
                <HeaderSwapping
                  title="Find permutation by code"
                  back={this.setTab.bind(this, null)}
                />
                <div className="p-2">
                  <div className="py-6 text-center">
                    <MdOutlinePassword
                      size={70}
                      className="mx-auto text-gray-400"
                    />
                    <h3 className="font-bold text-gray-700 text-2xl pt-2">
                      Find the permutation?
                    </h3>
                    <p className="pt-1 text-gray-600">
                      Please enter the code your friend gave you below
                    </p>

                    <div className="w-80 mx-auto mt-3 ">
                      {this.state.error.target === "search-code-error" && (
                        <Alert theme="danger">{this.state.error.msg}</Alert>
                      )}

                      <div
                        className={`pt-2  flex gap-1 items-center pb-1 w-full bg-gray-100 px-3 rounded  ${
                          this.state.error.target === "search-code-error"
                            ? "animate__animated  animate__shakeX"
                            : ""
                        }`}
                      >
                        <TextInput
                          onChange={(e) => {
                            // if (this.state.search_nid.length <= 6)
                            this.setState({
                              search_code: e.target.value,
                              error: {
                                msg: "",
                                target: null,
                              },
                            });
                          }}
                          disabled={this.state.loading_user_by_code}
                          className="flex-1"
                          value={this.state.search_code}
                          error={""}
                          label={""}
                          size="lg"
                          placeholder={""}
                          name={""}
                          type="number"
                          Input_className=""
                        />
                        <Button
                          size="lg"
                          className="w-"
                          loading={this.state.loading_user_by_code}
                          onClick={this.searchUserByCode}
                        >
                          Check
                        </Button>
                      </div>
                    </div>
                  </div>
                </div>
              </DashboardContainer>
            ) : (
              <DashboardContainer>
                <div className="flex border-b p-2 mb-2 ">
                  <h3 className="text-2xl font-bold text-gray flex-1">
                    Permutation
                  </h3>
                  <Button
                    theme="danger-light"
                    onClick={() =>
                      this.setState({
                        found_permutations: [],
                      })
                    }
                  >
                    Close
                  </Button>
                </div>
                <div className="">
                  {this.state.found_permutations.map((dataEl, i) => (
                    <PermutationItem
                      key={i}
                      more
                      permutation={dataEl}
                      approveUser={this.userApprovePermutationUser}
                    />
                  ))}
                </div>
              </DashboardContainer>
            )}
          </>
        )}

        {this.state.error.target === "approve-permutation-success" && (
          <Alert fixed theme="success">
            {this.state.error.msg}
          </Alert>
        )}
      </>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { auth: auth, basic: basic };
};

export const CandidateSwaping = connect(mapStateToProps, {})(_CandidateSwaping);
