import React, { FC, ReactElement } from "react";
import { MdWarning } from "react-icons/md";
import { PermutationItem__Interface } from "../../actions";

type SwapingListProps = {
  permutation: PermutationItem__Interface;
  i: number;
  selectPermutation: (id: string) => void;
};

const SwapingList: FC<SwapingListProps> = ({
  permutation,
  i,
  selectPermutation,
}): ReactElement => {
  let el = permutation;
  let user1 = el.usersInfo.find((itm) => itm.user_id === el.user_one);
  let user2 = el.usersInfo.find((itm) => itm.user_id === el.user_two);

  let school1 = el.schools.find((itm) => itm.school_code === el.school_one);
  let school2 = el.schools.find((itm) => itm.school_code === el.school_two);

  return (
    <>
      <tr>
        <td className="p-1"></td>
      </tr>
      <tr
        className={` cursor-pointer my-2 ${
          el.permutation_status === "Rejected"
            ? "hover:bg-red-200 bg-red-100"
            : "hover:bg-blue-200 bg-white"
        }`}
        onClick={selectPermutation.bind(this, el.permutation_id + "")}
      >
        <td className="border p-2 text-center">
          {el.permutation_status === "Rejected" ? (
            <span className="text-center text-red-800 text-lg">
              <MdWarning />
            </span>
          ) : (
            i + 1
          )}
        </td>
        <td className="border p-2 bg-blue-100 opacity-70">
          <h6 className="font-bold">{user1?.full_name}</h6>
          <span title="Staff code" className="text-sm text-gray-400">
            {user1?.staff_code}
          </span>
        </td>
        <td className="border p-2 bg-blue-200 opacity-90">
          <h6 className="font-bold">{school1?.school_name}</h6>
          <span title="School code" className="text-sm text-gray-400">
            {el.school_one}
          </span>
        </td>

        <td className="border p-2 bg-indigo-100 opacity-70">
          <h6 className="font-bold">{user2?.full_name}</h6>
          <span title="Staff code" className="text-sm text-gray-400">
            {user2?.staff_code}
          </span>
        </td>
        <td className="border p-2 bg-indigo-200 opacity-90">
          <h6 className="font-bold">{school2?.school_name}</h6>
          <span title="School code" className="text-sm text-gray-400">
            {el.school_two}
          </span>
        </td>

        <td className="border p-2">
          <h6 className="text-sm">
            User: <b>{el.permutation_status}</b>
          </h6>
          <hr />
          <h6 className="text-sm">
            REB: <b>{el.reb_status}</b>
          </h6>
        </td>

        <td className="border p-2">
          <h6 className="text-sm">
            Created: <b>{el.user_one_date}</b>
          </h6>
          <hr />
          <h6 className="text-sm">
            User 2: <b>{el.user_two_date}</b>
          </h6>
          <hr />
          <h6 className="text-sm">
            REB: <b>{el.user_two_date}</b>
          </h6>
        </td>
      </tr>
    </>
  );
};
export default SwapingList;
