import { Auth, SchoolPositions, TeacherData } from "../actions";
import { ProfileType } from "../types/userInfo";

export const ValidatePosition = (
  staff: TeacherData,
  schools_positions: SchoolPositions[]
): {
  position: {
    position_code: string | null;
    position_name: string | null;
    qualification_id: string | null;
    qualification_name: string | null;
  };
  match_position: boolean;
} => {
  let positionFound = schools_positions.find(
    (itm) => staff.position_code === itm.position_code
  );

  return {
    match_position: positionFound === undefined ? false : true,
    position:
      positionFound !== undefined
        ? {
            position_code: positionFound?.position_code,
            position_name: positionFound?.position_name,
            qualification_id: positionFound?.qualification_id,
            qualification_name: positionFound?.qualification_name,
          }
        : {
            position_code: "",
            position_name: staff.position,
            qualification_id: "",
            qualification_name: "",
          },
  };
};

// ********** Placement report validator ***********
const validateStaff = (
  staff: TeacherData
): { validated: boolean; errors: string[] } => {
  let errors: string[] = [];

  // first name
  if (!staff.first_name || staff.first_name.length <= 2)
    errors.push("First name is not valid");

  // last name
  if (!staff.last_name || staff.last_name.length <= 2)
    errors.push("Last name is not valid");

  // // bank name
  // if (!staff.bank_name || staff.bank_name.length <= 2)
  //   errors.push("Bank name is not valid");

  // // bank account
  // if (!staff.bank_account || staff.bank_account.length <= 2)
  //   errors.push("Bank account number is not valid");

  // bank account
  if (!staff.full_name || staff.full_name.length <= 2)
    errors.push("Full names");

  // hired date
  if (!staff.hired_date || staff.hired_date.length <= 2)
    errors.push("Hired date is not valid");

  // martial status
  if (!staff.marital_status || staff.marital_status.length <= 2)
    errors.push("Martial status not valid");

  // National status
  if (!staff.nationality || staff.nationality.length <= 2)
    errors.push("Nationality is not valid");

  // National status
  if (!staff.nid || staff.nid.length <= 4) errors.push("NID is not valid");

  // Phone number
  if (!staff.phone_numbers || staff.phone_numbers.length <= 8)
    errors.push("Phone number is not valid");

  // Phone number
  if (!staff.position || staff.position.length <= 2)
    errors.push("No selected position");

  // Qualification
  if (!staff.qualification || staff.qualification.length <= 1)
    errors.push("No selected qualification");

  // Role
  if (!staff.role || staff.role.length <= 2) errors.push("No selected role");

  // // RSSB number
  // if (!staff.rssb_number || staff.rssb_number.length <= 4)
  //   errors.push("Invalid RSSB Number");

  // School
  if (
    !staff.school ||
    !staff.school?.school_name ||
    staff.school?.school_name.length <= 1
  )
    errors.push("Invalid School");

  // No sex selected
  if (!staff.sex || staff.sex.length <= 2) errors.push("No sex selected");

  // Specialization
  if (!staff.specialization || staff.specialization.length <= 2)
    errors.push("Invalid specialization");

  // Specialization
  if (!staff.staff_code || staff.staff_code.length <= 2)
    errors.push("Invalid Staff category");

  // Hierarch
  if (staff.role_id === "1")
    if (!staff.teacher_hierarch || staff.teacher_hierarch.length <= 0)
      errors.push("This user is a teacher, and must have at least one course");

  // Role
  if (!staff.role || staff.role.length <= 0)
    errors.push("Your role id is not validated");

  if (!staff.teaching_level_id || staff.teaching_level_id.length <= 2)
    errors.push("Teaching level does not found");

  if (
    !staff.education_certificate_id ||
    staff.education_certificate_id.length <= 2
  )
    errors.push("Education certificate does not found");

  return {
    errors: errors,
    validated: errors.length > 0 ? false : true,
  };
};

