import React, { FC, ReactElement, useState } from "react";
import { FaSchool } from "react-icons/fa";
import { MdAccountBox, MdSwapHorizontalCircle } from "react-icons/md";
import {
  PermutationItem__Interface,
  PermutationSchool__Interface,
  PermutationUser__Interface,
} from "../../actions";
import Button from "../../components/Buttons/Button";

type PermutationItemProps = {
  permutation: PermutationItem__Interface;
  more?: boolean;
  approveReb?: (id: string | null) => void;
  approveUser?: (id: string | null) => void;
  loading?: boolean;
  close?: () => void;
  userRejectPermutation?: (id: string | null) => void;
  loading_reject_permutation?: boolean;
};

const PermutationItem: FC<PermutationItemProps> = ({
  permutation,
  more = false,
  approveUser,
  approveReb,
  userRejectPermutation,
  loading = false,
  loading_reject_permutation,
  close,
}): ReactElement => {
  const [expandItem, setExpand] = useState(false);

  let expand = more === true ? true : expandItem;
  let user_1 = permutation.usersInfo.find(
    (itm) => itm.user_id === permutation.user_one
  );
  let user_2 = permutation.usersInfo.find(
    (itm) => itm.user_id === permutation.user_two
  );
  let school_1 = permutation.schools.find(
    (itm) => itm.school_code === permutation.school_one
  );
  let school_2 = permutation.schools.find(
    (itm) => itm.school_code === permutation.school_two
  );

  let UserComponent = (
    user: PermutationUser__Interface | undefined,
    school: PermutationSchool__Interface | undefined
  ) => {
    if (user === undefined || school === undefined)
      return <div className="p-2 text-center text-lg">No users found</div>;

    return (
      <div className={`flex-1 w-full rounded bg-white border`}>
        <div
          className={`rounded m-1 border flex ${
            expand === true ? "" : "items-center"
          }`}
        >
          <section>
            <MdAccountBox className="text-gray-400" size={expand ? 80 : 30} />
          </section>
          <div className="flex-1 pl-2">
            {[
              {
                label: "Names: ",
                value: user.full_name,
                more: true,
                inline: false,
              },
              {
                label: "Staff code: ",
                value: user.staff_code,
                more: false,
                inline: true,
              },
              {
                label: "NID: ",
                value: user.nid,
                more: false,
                inline: true,
              },
              {
                label: "Sex: ",
                value: user.sex,
                more: false,
                inline: true,
              },
              {
                label: "Phone number: ",
                value: user.phone_numbers,
                inline: true,
                more: false,
              },
              {
                label: "Martial status: ",
                value: user.marital_status,
                inline: true,
                more: false,
              },
            ].map((itm, i) => {
              if (itm.more === false && expand === false) return null;
              if (!itm.value) return null;
              return (
                <section
                  key={i}
                  className={`border-b ${
                    itm.inline === true ? "flex py-1 items-center" : ""
                  }`}
                >
                  <span className="text-sm text-gray-500 block">
                    {itm?.label}
                  </span>
                  <h2
                    className={` ${
                      itm.inline === false ? "-mt-1" : "pl-2"
                    } font-bold block `}
                  >
                    {itm?.value}
                  </h2>
                </section>
              );
            })}
          </div>
        </div>

        <div className="rounded m-1 border flex items-center mt-3">
          <section className="px-4">
            <FaSchool className="text-gray-400" size={expand ? 50 : 20} />
          </section>
          <div className="flex-1">
            {[
              {
                label: "School code: ",
                value: school.school_code,
                inline: true,
                more: true,
              },
              {
                label: "School name: ",
                value: school.school_name,
                inline: true,
                more: false,
              },
            ].map((itm, i) => {
              if (itm.more === true && expand === false) return null;
              if (!itm.value) return null;
              return (
                <section
                  key={i}
                  className={`border-b ${
                    itm.inline === true ? "flex py-1 items-center" : ""
                  }`}
                >
                  <span className="text-sm text-gray-500 block">
                    {itm?.label}
                  </span>
                  <h2
                    className={` ${
                      itm.inline === false ? "-mt-1" : "pl-2"
                    } font-bold block `}
                  >
                    {itm?.value}
                  </h2>
                </section>
              );
            })}
          </div>
        </div>
      </div>
    );
  };

  return (
    <div className="bg-white p-2 mb-3 rounded animate__animated animate__zoomIn animate__faster">
      {close && !loading && (
        <div className="text-right pb-2 border-b mb-2">
          <Button className="" onClick={close} theme="danger-light">
            Close
          </Button>
        </div>
      )}
      <div
        className="items-center flex flex-col md:flex-row cursor-pointer"
        onClick={setExpand.bind(this, !expand)}
      >
        {UserComponent(user_1, school_1)}
        <div className="-mx-2 z-40 ">
          <MdSwapHorizontalCircle
            className={`rounded ${
              permutation.reb_status === "Approved"
                ? "text-green-500"
                : "text-accent-900"
            } `}
            size={50}
          />
        </div>
        {UserComponent(user_2, school_2)}
      </div>
      {expand && (
        <div className="pt-3">
          <div className="p-1 px-2 rounded mt-2 border grid lg:grid-cols-3 gap-1 ">
            {[
              {
                label: "Permutation Code: ",
                value: permutation.permutation_code,
                inline: true,
              },
              {
                label: "Position: ",
                value: `${permutation.position_code} - ${permutation.position_name}`,
                inline: true,
              },
              {
                label: "Qualification name: ",
                value: permutation.qualification_name,
                inline: true,
              },
              {
                label: "Qualification name: ",
                value: permutation.role,
                inline: true,
              },
              {
                label: "Initiated date: ",
                value: permutation.user_one_date,
                inline: true,
              },
              {
                label: "Accepted date: ",
                value: permutation.user_two_date,
                inline: true,
              },
            ].map((itm, i) => (
              <section
                key={i}
                className={`border-b border-l pl-2 ${
                  itm.inline === true ? "flex py-1 items-center" : ""
                }`}
              >
                <span className="text-sm text-gray-500 block">
                  {itm?.label}
                </span>
                <h2
                  className={` ${
                    itm.inline === false ? "-mt-1" : "pl-2"
                  } font-bold block `}
                >
                  {itm?.value}
                </h2>
              </section>
            ))}
          </div>

          <div className="grid grid-cols-2 pt-3 items-center">
            <div className="flex gap-1">
              {permutation.reb_status && (
                <section
                  className={`flex items-center gap-1 px-2 py-0.5 rounded-lg  ${
                    permutation.permutation_status === "Accepted"
                      ? "bg-green-100 text-green-900"
                      : "bg-accent-400 text-accent-900"
                  }`}
                >
                  <span className="text-xs opacity-70">User status</span>
                  <h5 className={`text  block  font-bold `}>
                    {permutation.permutation_status}
                  </h5>
                </section>
              )}

              {permutation.reb_status && (
                <section
                  className={`flex items-center gap-1 px-2 py-0.5 rounded-lg  ${
                    permutation.reb_status === "Approved"
                      ? "bg-green-100 text-green-900"
                      : "bg-accent-400 text-accent-900"
                  }`}
                >
                  <span className="text-xs opacity-70">REB status</span>
                  <h5 className={`text  block  font-bold `}>
                    {permutation.reb_status}
                  </h5>
                </section>
              )}
            </div>
            <div className="text-right grid gap-2">
              {approveUser && (
                <Button
                  onClick={approveUser.bind(this, permutation.permutation_id)}
                  loading={loading}
                  size="lg"
                >
                  Accept permutation
                </Button>
              )}

              {userRejectPermutation && (
                <Button
                  onClick={userRejectPermutation.bind(
                    this,
                    permutation.permutation_id
                  )}
                  loading={loading_reject_permutation}
                  size="lg"
                  theme="danger-light"
                >
                  Reject permutation
                </Button>
              )}

              {approveReb && (
                <>
                  {permutation.reb_status !== "Approved" && (
                    <Button
                      onClick={approveReb.bind(
                        this,
                        permutation.permutation_id
                      )}
                      loading={loading}
                      size="lg"
                    >
                      Approve permutation
                    </Button>
                  )}
                </>
              )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default PermutationItem;
